@charset "UTF-8";
:root {
  --ff-body: "Outfit", sans-serif;
  --ff-heading: "Young Serif", serif;
}

:root {
  --bg-clr-li: hsl(30, 54%, 90%);
  --bg-clr-dm: hsl(30, 54%, 20%);
  --main-clr-li: hsl(30, 10%, 34%);
  --main-clr-dm: hsl(30, 10%, 89%);
  --secondary-clr-li: hsl(24, 5%, 31%);
  --secondary-clr-dm: hsl(24, 5%, 88%);
  --h2-clr-li: hsl(14, 45%, 52%);
  --h2-clr-dm: hsl(14, 45%, 86%);
  --h3-clr-li: hsl(332, 60%, 52%);
  --h3-clr-dm: hsl(332, 51%, 82%);
  --brdr-clr-li: hsl(30, 18%, 87%);
  --brdr-clr-dm: hsl(30, 18%, 37%);
  --white-li: hsl(0, 0%, 100%);
  --white-dm: hsl(0, 0%, 25%);
  --about-clr-li: hsl(0, 0%, 95%);
  --about-clr-dm: hsl(0, 0%, 10%);
  --logo-bg-li: hsl(0, 0%, 10%);
  --logo-bg-dm: hsl(0, 0%, 80%);
  --logo-clr-li: hsl(119, 50%, 69%);
  --logo-clr-dm: hsl(119, 50%, 30%);
  --about-clr: var(--about-clr-li);
  --logo-bg: var(--logo-bg-li);
  --logo-clr: var(--logo-clr-li);
  --main-clr: var(--main-clr-li);
  --secondary-clr: var(--secondary-clr-li);
  --bg-clr: var(--bg-clr-li);
  --h2-clr: var(--h2-clr-li);
  --h3-clr: var(--h3-clr-li);
  --brdr-clr: var(--brdr-clr-li);
  --white: var(--white-li);
}

.dark-mode {
  --main-clr: var(--main-clr-dm);
  --secondary-clr: var(--secondary-clr-dm);
  --bg-clr: var(--bg-clr-dm);
  --h2-clr: var(--h2-clr-li);
  --h3-clr: var(--h3-clr-li);
  --brdr-clr: var(--brdr-clr-li);
  --white: var(--white-dm);
  --about-clr: var(--about-clr-dm);
  --logo-bg: var(--logo-bg-dm);
  --logo-clr: var(--logo-clr-dm);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  height: 100%;
}
html:focus-within {
  scroll-behavior: smooth;
}

*,
*::after,
*::before {
  box-sizing: inherit;
  font: inherit;
}

body {
  margin: 0;
  height: 100%;
  padding: 0;
  text-rendering: optimizeSpeed;
  font-family: var(--ff-body);
  overflow-x: hidden;
}

.center {
  display: grid;
  align-items: center;
  height: 100%;
  position: relative;
}

h1,
h2,
h3,
blockquote,
p {
  margin: 0;
}

a,
a:visited,
a:active {
  color: hsl(0, 0%, 96%);
  text-decoration: none;
}

ul,
ol,
li {
  list-style: none;
  padding-left: 0;
  margin: 0;
}

img {
  display: block;
  object-fit: cover;
  max-width: 100%;
}

.flex {
  display: flex;
  align-items: center;
}

@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
@keyframes scale-left-logo {
  0% {
    max-width: 0rem;
    font-size: 0rem;
  }
  100% {
    font-size: 0.9375rem;
    max-width: 28.125rem;
  }
}
.btns {
  position: absolute;
  width: 100%;
  top: 3%;
  right: 0;
  z-index: 5;
}
.btns button {
  position: absolute;
  font-size: 1.1875rem;
  top: 3%;
  width: 9.375rem;
  border: none;
  background-color: hsl(0, 0%, 36%);
  border-radius: 25%;
  text-transform: capitalize;
  cursor: pointer;
  color: var(--logo-clr);
  font-weight: bold;
}
.btns .dark-mode-btn {
  right: 2%;
}
.btns span {
  display: none;
}

.dark-mode .btns button {
  background-color: hsl(0, 0%, 96%);
}

#remove-events {
  position: absolute;
  z-index: -1;
  height: 100%;
  width: 100%;
}

.center {
  z-index: -2;
  justify-content: center;
}

body {
  background-color: var(--bg-clr);
  z-index: -5;
  position: relative;
}

main {
  display: grid;
  justify-items: center;
}

.card {
  background-color: var(--white);
  max-width: min(46rem, 100%);
  border-radius: 25px;
  padding: 2.5rem;
  margin: 7.6875rem 0;
  box-shadow: 0 0.625rem 1.25rem rgba(0, 0, 0, 0.2);
}
.card .header img {
  max-width: min(41rem, 100%);
  margin-bottom: 2rem;
  border-radius: 15px;
}
.card .header h1 {
  font-family: var(--ff-heading);
  font-size: 2.5rem;
  color: var(--secondary-clr);
  margin-bottom: 1.125rem;
}
.card .header p {
  font-size: 1rem;
  line-height: 1.49;
  color: var(--main-clr);
  margin-bottom: 2.25rem;
}
.card .preparation {
  margin-bottom: 1.6875rem;
  background-color: hsla(332, 51%, 32%, 0.1);
  border-radius: 25px;
  padding: 1.3125rem 1.75rem;
}
.card .preparation h3 {
  font-family: var(--ff-body);
  font-size: 1.25rem;
  font-weight: 800;
  color: var(--h3-clr);
  margin-bottom: 0.75rem;
}
.card .preparation ul {
  padding-left: 0.5rem;
  padding-left: 0.5rem;
}
.card .preparation ul li:before {
  content: "·";
  font-size: 1.25rem;
  font-weight: 800;
  line-height: 1;
  vertical-align: top;
  margin-right: 1.6875rem;
}
.card .preparation ul li.time {
  display: flex;
}
.card .preparation ul li.time p {
  margin-left: 0.15625rem;
  display: inline;
}
.card .preparation ul li {
  font-size: 1rem;
  color: var(--main-clr);
}
.card .preparation ul li:not(:last-child) {
  margin-bottom: 0.875rem;
}
.card .preparation ul li span {
  color: var(--secondary-clr);
  font-weight: 700;
}
.card .ingredients {
  margin-bottom: 1.875rem;
  padding-bottom: 1.875rem;
  border-bottom: 1px solid var(--brdr-clr);
}
.card .ingredients h2 {
  font-family: var(--ff-heading);
  font-size: 1.75rem;
  color: var(--h2-clr);
  margin-bottom: 0.9375rem;
}
.card .ingredients ul {
  padding-left: 0.5rem;
}
.card .ingredients ul li:before {
  content: "·";
  font-size: 1.25rem;
  font-weight: 800;
  line-height: 1;
  vertical-align: top;
  margin-right: 1.6875rem;
}
.card .ingredients ul li {
  font-size: 1rem;
  color: var(--main-clr);
}
.card .ingredients ul li:not(:last-child) {
  margin-bottom: 0.4375rem;
}
.card .ingredients ul li.time {
  display: flex;
}
.card .ingredients ul li.time p {
  display: inline;
}
.card .instructions {
  margin-bottom: 1.625rem;
  padding-bottom: 1.875rem;
  border-bottom: 1px solid var(--brdr-clr);
}
.card .instructions h2 {
  font-family: var(--ff-heading);
  font-size: 1.75rem;
  color: var(--h2-clr);
  margin-bottom: 0.9375rem;
}
.card .instructions ol {
  padding-left: 0.3125rem;
  counter-reset: list-item-counter;
}
.card .instructions ol li::before {
  content: counter(list-item-counter) ". ";
  font-weight: 900;
  color: var(--h2-clr);
}
.card .instructions ol li {
  display: flex;
}
.card .instructions ol li p {
  margin-left: 1.125rem;
  display: inline;
}
.card .instructions ol li {
  counter-increment: list-item-counter;
  font-size: 1rem;
  color: var(--main-clr);
  line-height: 1.5;
}
.card .instructions ol li:not(:last-child) {
  margin-bottom: 0.5625rem;
}
.card .instructions ol li span {
  font-weight: 700;
  color: var(--secondary-clr);
}
.card .nutrition h2 {
  font-family: var(--ff-heading);
  font-size: 1.75rem;
  color: var(--h2-clr);
  margin-bottom: 1.25rem;
}
.card .nutrition p {
  font-size: 1rem;
  color: var(--main-clr);
  line-height: 1.5;
  margin-bottom: 1rem;
}
.card .nutrition ul li {
  padding: 0.875rem 1.8125rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  color: var(--main-clr);
}
.card .nutrition ul li:not(:last-child) {
  border-bottom: 1px solid var(--brdr-clr);
}
.card .nutrition ul li span:nth-child(2) {
  color: var(--h2-clr);
  font-weight: 700;
  font-size: 1rem;
}

@media (max-width: 37.5em) {
  main .card {
    background-color: var(--white);
    border-radius: 25px;
    padding: 0rem;
    margin: 0;
    margin-top: 1.5625rem;
    box-shadow: none;
  }
  main .card .padding-inline {
    padding-inline: 1.875rem;
  }
  main .card .header img {
    margin-bottom: 2.3125rem;
    border-radius: 0;
  }
  main .card .header h1 {
    font-size: 2.25rem;
    margin-bottom: 1.375rem;
    line-height: 1;
  }
  main .card .header p {
    font-size: 1rem;
    line-height: 1.5;
  }
  main .card .preparation {
    margin: 0 2.0625rem 1.625rem 2.0625rem;
    border-radius: 10px;
    padding: 1.25rem 1.5rem;
    padding-left: 2.5rem;
  }
  main .card .preparation h3 {
    font-size: 1.225rem;
    font-weight: bold;
    margin-bottom: 0.875rem;
  }
  main .card .preparation ul li:before {
    font-size: 1rem;
    margin-right: 0.625rem;
  }
  main .card .preparation ul li {
    line-height: 1.49;
  }
  main .card .preparation ul li:not(:last-child) {
    margin-bottom: 0.5rem;
  }
  main .card .ingredients h2 {
    margin-bottom: 1.1875rem;
  }
  main .card .ingredients ul {
    padding-left: 0.4375rem;
  }
  main .card .ingredients ul li:before {
    content: "·";
    font-size: 1.375rem;
    margin-right: 1.875rem;
  }
  main .card .ingredients ul li:not(:last-child) {
    margin-bottom: 0.6875rem;
  }
  main .card .instructions {
    margin-bottom: 1.5rem;
  }
  main .card .instructions h2 {
    margin-bottom: 1.125rem;
  }
  main .card .instructions ol {
    padding-left: 0.625rem;
  }
  main .card .instructions ol li p {
    margin-left: 1rem;
  }
  main .card .instructions ol li:not(:last-child) {
    margin-bottom: 0.5625rem;
  }
  main .card .nutrition h2 {
    margin-bottom: 1.09375rem;
  }
  main .card .nutrition p {
    line-height: 1.49;
    margin-bottom: 0.8125rem;
  }
  main .card .nutrition ul li {
    padding: 0.875rem 2rem;
  }
  .center .btns button {
    right: 0%;
    top: -1.3125rem;
    border-radius: 0;
    width: min(36.25rem, 100%);
  }
}
.attribution {
  font-size: 0.9375rem;
  text-align: center;
  background-color: var(--logo-bg);
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  max-width: fit-content;
  color: var(--about-clr);
  border-radius: 2.5rem;
  gap: 0.9375rem;
  margin-inline: 1.375rem;
  position: fixed;
  bottom: 2%;
  right: 1%;
}
.attribution #about {
  margin-left: 0.9375rem;
  animation: scale-left-logo 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}
.attribution #about.none {
  display: none;
}
.attribution #logo {
  cursor: pointer;
}
.attribution #logo img {
  border-radius: 50%;
  max-width: 5rem;
}
.attribution a {
  color: var(--logo-clr);
  font-weight: bold;
}

.none {
  display: none;
}